.Input{
    width: 100%;
    height: 40px; 
    background: #280731;
    border: 1px solid #4D2D55;
    box-sizing: border-box;
    box-shadow: 0px 0px 30px rgba(255, 70, 91, 0.15);
    border-radius: 20px;
    font-family: GothamProLight;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 15px;
    color: #908C91;
    padding-left: 20px;
    outline: none;
}
